import React from 'react'

class SetMeeting extends React.Component {

    onSubmit = e => {
        e.preventDefault()
        console.log('button pressed')
    }

    render() {
        return(
            <form onSubmit = { this.onSubmit }>
                <label htmlFor='title'>Name of Meetup:</label>
                <input id='title' type='text'></input>
                <label htmlFor='dayOfWeek'>Day of the Week:</label>
                <input id='dayOfWeek' type='text'></input>
                <label htmlFor='weekOfMonth' type='text'>Week of the Month:</label>
                <input id='weekOfMonth' type='text'></input>
                <label htmlFor='rsvp' type='text'>RSVP Link:</label>
                <input id='rsvp'></input>
                <label htmlFor='email'>Host Email:</label>
                <input id='email' type='email'></input>
                <button>Submit</button>
            </form>
        )
    }
}

export default SetMeeting